.Tags {
    display: flex;
    flex-direction: column;
    align-items: center;

    &__container {
        display: flex;
        flex-wrap: wrap;
        gap: 8px;
        align-items: center;

        span {
            color: #fff;
            background: rgba(243, 243, 243, 0.38);
            border-radius: 12.8px;
            padding: 4px 10px;
            font-size: 14px;
        }
    }

    &__icon {
        width: 36px;

        img {
            width: 100%;
        }
    }

    &__pt50 {
        padding-top: 50px;
    }

    &__button {
        cursor: pointer;
        margin: 50px auto;
        padding: 15px;
        width: max-content;
        border-radius: 5px;
        background: rgba(255, 82, 82, 1);
        box-shadow: 0 0 0 0 rgba(255, 82, 82, 1);
        animation: pulse 2s infinite;
    }
}