.Header__title {
  background: linear-gradient(90.59deg, rgba(255, 38, 142, 0.99) 1.51%, rgba(255, 107, 0, 0.994844) 54.54%, #EA2EEE 103.33%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  font-weight: 800;
  text-align: center;
  font-size: 34px;
  padding-top: 30px;
}
@media screen and (min-width: 430px) {
  .Header__title {
    font-size: 48px;
  }
}
.Header__image {
  width: 100%;
  transform-origin: 50% 50%;
  transform-style: preserve-3d;
  perspective: 1000px;
  animation: transform 20s ease-in-out infinite both alternate;
  pointer-events: none;
  -moz-user-select: none;
  -webkit-user-select: none;
          user-select: none;
}
.Header__pt50 {
  padding-top: 50px;
}/*# sourceMappingURL=Header.css.map */