.Loading {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
.Loading img {
  width: 70px;
  margin-left: -500px;
  animation: loading 2s ease-in-out;
  transform: rotate(90deg);
}
@keyframes loading {
  to {
    margin-left: 500px;
  }
}/*# sourceMappingURL=Loading.css.map */