.Tags {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.Tags__container {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
  align-items: center;
}
.Tags__container span {
  color: #fff;
  background: rgba(243, 243, 243, 0.38);
  border-radius: 12.8px;
  padding: 4px 10px;
  font-size: 14px;
}
.Tags__icon {
  width: 36px;
}
.Tags__icon img {
  width: 100%;
}
.Tags__pt50 {
  padding-top: 50px;
}
.Tags__button {
  cursor: pointer;
  margin: 50px auto;
  padding: 15px;
  width: -moz-max-content;
  width: max-content;
  border-radius: 5px;
  background: rgb(255, 82, 82);
  box-shadow: 0 0 0 0 rgb(255, 82, 82);
  animation: pulse 2s infinite;
}/*# sourceMappingURL=Tags.css.map */