.About {
    height: 100vh;

    &__area {
        margin-top: 30px;
        background-color: rgb(0, 0, 0);
        color: green;
        opacity: 1;
        border: 5px solid green;
        padding: 20px;
        font-size: 18px;
    }

    &__resume {
    padding-top: 40px;

        p {
            font-size: 18px;
        }

        ul {
            padding: 20px;
        }

        li {
            list-style-type: none;
        }
    }

    &__contacts {
        padding-top: 50px;
        
        img {
            width: 50px;
            height: 50px;
        }

        p {
            padding: 10px 0;
            font-size: 20px;
        }

        &_elem {
            padding-top: 10px;
            display: flex;
            align-items: center;
        }
    }

    &__button {
        cursor: pointer;
        margin: 50px auto;
        padding: 15px;
        width: max-content;
        border-radius: 5px;
        background: rgba(255, 82, 82, 1);
        box-shadow: 0 0 0 0 rgba(255, 82, 82, 1);
    }

}