.About {
  height: 100vh;
}
.About__area {
  margin-top: 30px;
  background-color: rgb(0, 0, 0);
  color: green;
  opacity: 1;
  border: 5px solid green;
  padding: 20px;
  font-size: 18px;
}
.About__resume {
  padding-top: 40px;
}
.About__resume p {
  font-size: 18px;
}
.About__resume ul {
  padding: 20px;
}
.About__resume li {
  list-style-type: none;
}
.About__contacts {
  padding-top: 50px;
}
.About__contacts img {
  width: 50px;
  height: 50px;
}
.About__contacts p {
  padding: 10px 0;
  font-size: 20px;
}
.About__contacts_elem {
  padding-top: 10px;
  display: flex;
  align-items: center;
}
.About__button {
  cursor: pointer;
  margin: 50px auto;
  padding: 15px;
  width: -moz-max-content;
  width: max-content;
  border-radius: 5px;
  background: rgb(255, 82, 82);
  box-shadow: 0 0 0 0 rgb(255, 82, 82);
}/*# sourceMappingURL=About.css.map */