.App {
    margin: 0 auto;
    max-width: 660px;
    position: relative;
}

body::before {
    content: '';
    position: fixed;
    top: 0;
    left: 0;
    max-width: 200px;
    width: 100%;
    height: 300px;
    background: #fc2d7f;
    filter: blur(250px);
    -webkit-filter: blur(250px);
    -moz-filter: blur(250px);
    -ms-filter: blur(250px);
    z-index: -1;
    animation: transform 10s ease-in-out infinite both alternate;
}
